import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import { Menu, Dropdown } from 'antd'
import { DownOutlined } from '@ant-design/icons'
import { withRouter } from 'react-router-dom'
import Icon from '../../../components/light/Icon'

function Header(props) {
  const { isSidebarVisible } = props

  const handleArrow = () => {
    props.onSetState(['isSidebarVisible'], !isSidebarVisible)
    localStorage.setItem('isSidebarVisible', !isSidebarVisible)
  }

  const handleQuit = () => {
    props.history.push('/')
  }

  const handleTheme = (theme) => {
    props.onSetState(['theme'], theme)
    localStorage.setItem('theme', JSON.stringify(theme))
  }

  useEffect(() => {
    const isSidebarVisible =
      JSON.parse(localStorage.getItem('isSidebarVisible')) !== false
    props.onSetState(['isSidebarVisible'], isSidebarVisible)
    let theme = JSON.parse(localStorage.getItem('theme'))
    theme = theme ? theme : 'dark'
    props.onSetState(['theme'], theme)
  }, [props])

  const userMenu = (
    <Menu>
      <Menu.Item>更改密码</Menu.Item>
      <Menu.Item onClick={() => handleQuit()}>退出</Menu.Item>
    </Menu>
  )

  const themeMenu = (
    <Menu>
      <Menu.Item onClick={() => handleTheme('dark')}>默认(深色)</Menu.Item>
      <Menu.Item onClick={() => handleTheme('light')}>浅色</Menu.Item>
    </Menu>
  )

  return (
    <div className="m-list-header">
      <Icon
        name="arrow"
        className={`m-list-header-icon ${isSidebarVisible ? '' : 'rotate'}`}
        onClick={() => handleArrow()}
      ></Icon>
      <div className="m-list-header-info"></div>
      <div className="m-list-header-menu">
        <span className="m-list-header-menu-welcome">欢迎您</span>
        <Dropdown overlay={userMenu}>
          <span className="ant-dropdown-link">
            <span className="m-list-header-menu-username">admin</span>
            <DownOutlined />
          </span>
        </Dropdown>
        <Dropdown overlay={themeMenu}>
          <span className="m-list-header-menu-theme">
            <Icon name="theme"></Icon>
          </span>
        </Dropdown>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    isSidebarVisible: state.getIn(['light', 'isSidebarVisible']),
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetState(key, value) {
      dispatch({ type: 'SET_LIGHT_STATE', key, value })
    },
    onDispatch(action) {
      dispatch(action)
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Header))
