import React from 'react'
import { Menu } from 'antd'
import { connect } from 'react-redux'
import { NavLink, withRouter } from 'react-router-dom'
import Icon from '../../../components/light/Icon'
import Logo from './Logo'

const { SubMenu } = Menu

function Sidebar(props) {
  const { router, location: {pathname}, isSidebarVisible, theme } = props
  const defaultOpenKeysFn = () => {
    const arr = []
    let myPathname = pathname.replace(/(\/+$)/g, '')
    let arrTemp = myPathname.split('/')
    if(arrTemp.length > 4) {
      for (let i = 4; i < arrTemp.length; i++) {
        arr.push(arrTemp.slice(0, i).join('/'))
      }
    }
    return arr
  }

  const sidebarRender = (arr) => {
    return arr.map((item) => {
      if (item.children) {
        return (
          <SubMenu
            key={item.key}
            title={item.title}
            icon={item.icon && (
              <Icon name={item.icon} className="m-sidebar-icon"></Icon>
            )}
            className={item.className ? item.className : ''}
          >
            {sidebarRender(item.children)}
          </SubMenu>
        )
      } else {
        return (
          <Menu.Item
            key={item.path}
            className={item.className ? item.className : ''}
          >
            {item.icon && (
              <Icon name={item.icon} className="m-sidebar-icon"></Icon>
            )}
            <NavLink to={item.path}>{item.title}</NavLink>
          </Menu.Item>
        )
      }
    })
  }

  const sidebarDom = sidebarRender(router)

  return (
    <div className={`m-sidebar ${isSidebarVisible ? 'active' : ''}`}>
      <Menu
        defaultSelectedKeys={[pathname]}
        defaultOpenKeys={defaultOpenKeysFn()}
        style={{ width: 256 , height: '100%', overflowY: 'auto'}}
        mode="inline"
        theme={theme}
      >
        <Logo></Logo>
        {sidebarDom}
      </Menu>
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    router: state.getIn(['light', 'router']).toJS(),
    isSidebarVisible: state.getIn(['light', 'isSidebarVisible']),
    theme: state.getIn(['light', 'theme']),
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetState(key, value) {
      dispatch({ type: 'SET_LIGHT_STATE', key, value })
    },
    onDispatch(action) {
      dispatch(action)
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Sidebar))
